.destinations-description_wrap {
  margin-bottom: 110px;
}

.destinations-description_wrap .destinations-description_content {
  padding: 60px 80px;
  background-color: #F6F6F6;
}

.destinations-description_wrap .destinations-description_content p {
  font-size: 15px;
  line-height: 22px;
}

.destinations-description_wrap .destinations-description_content p:first-child {
  font-size: 19px;
  line-height: 28px;
  margin-bottom: 15px;
}

.destinations-content_item {
  display: flex;
  justify-content: space-between;
  padding-bottom: 110px;
  margin-bottom: 110px;
  border-bottom: 2px solid #E9E9E9;
}

.destinations-content_item:last-child {
  margin-bottom: 80px;
}

.destinations-content_item:nth-child(even) {
  flex-direction: row-reverse;
}

.destinations-content_item .left {
  width: 50%;
}

.destinations-content_item .left .destinations-item_description {
  margin-bottom: 30px;
}

.destinations-content_item .left h4 {
  font-size: 36px;
  line-height: 50px;
  margin-bottom: 15px;
}

.destinations-content_item .left p {
  font-size: 15px;
  line-height: 22px;
  margin-bottom: 15px;
}

.destinations-content_item .left p:last-child {
  margin-bottom: 0;
}

.destinations-content_item .left .destination-button {
  width: auto;
  padding: 15px 24px;
  display: inline-block;
}

.destinations-content_item .right {
  width: 40%;
  height: 600px;
  border-radius: 4px;
  overflow: hidden;
}

.destinations-content_item .right:hover img {
  transform: scale(1.1);
}

.destinations-content_item .right img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: .3s all ease;
}

@media screen and (max-width: 990px) {
  .destinations-description_wrap {
    margin-bottom: 40px;
    padding-bottom: 40px;
  }
  .destinations-description_wrap .destinations-description_content {
    padding: 30px 60px;
  }
  .destinations-content_item {
    flex-direction: column-reverse;
  }
  .destinations-content_item:nth-child(even) {
    flex-direction: column-reverse;
  }
  .destinations-content_item .left {
    width: 100%;
  }
  .destinations-content_item .right {
    width: 100%;
    margin-bottom: 40px;
  }
}

@media screen and (max-width: 500px) {
  .destinations-description_wrap {
    margin-bottom: 30px;
    padding-bottom: 30px;
  }
  .destinations-description_wrap .destinations-description_content {
    padding: 15px;
  }
  .destinations-description_wrap .destinations-description_content p {
    font-size: 16px;
    line-height: 23px;
  }
  .destinations-description_wrap .destinations-description_content p:first-child {
    font-size: 20px;
    line-height: 28px;
    margin-bottom: 10px;
  }
  .destinations-content_item {
    padding-bottom: 30px;
    margin-bottom: 30px;
  }
  .destinations-content_item:last-child {
    margin-bottom: 30px;
  }
  .destinations-content_item .left .destinations-item_description {
    margin-bottom: 15px;
  }
  .destinations-content_item .left h4 {
    font-size: 26px;
    line-height: 36px;
    margin-bottom: 4px;
  }
  .destinations-content_item .left p {
    font-size: 14px;
    line-height: 23px;
  }
  .destinations-content_item .right {
    margin-bottom: 25px;
    height: 290px;
  }
}

